import React, { Component } from 'react'
import LeftGrid from "./Left Grid/LeftGrid.js"
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import HomeGrid from './HomeGrid/HomeGrid.js';
import SliderC from './Carousel.js'
import BrandGrid from './Left Grid/BrandGrid.js';
import GroceryGrid from './Left Grid/GroceryGrid.js';
import BrandLogoGrid from './Left Grid/BrandLogoGrid.js';
import WebsiteLogoGrid from './Left Grid/WebsiteLogoGrid.js';
import ramadan from './RamadanData.js'
import MallGrid from './Left Grid/MallGrid.js';
import Mall from './MallpageGrid/Mall';
import RedMallGrid from './Left Grid/RedMallGrid.js';
import Ads1 from './ads/ads1'
import Ads2 from './ads/ads2'
import './Home.css'
const styles = theme => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing.unit * 2,
      textAlign: 'center',
      color: theme.palette.text.secondary,
    },
  });

export default class Home extends Component {
  constructor(props){
    super(props)

    this.ramadanimg = ramadan
    this.state = { backgroundIndex: 0, animDuration: 1 }
    this.changeBackground = this.changeBackground.bind(this)
  }

  componentDidMount(){
    this.timeout = setTimeout(
      this.changeBackground,
      this.state.animDuration * 3000
    )
  }

  componentWillUnmount(){
    if (this.timeout) clearTimeout(this.timeout)
    // clearInterval(this.timer);
   }

   changeBackground () {
    this.setState(function ({ backgroundIndex }) {
      const nextBackgroundIndex = ++backgroundIndex % this.ramadanimg.length

      return { backgroundIndex: nextBackgroundIndex }
    }, function () {
      this.timeout = setTimeout(
        this.changeBackground,
        this.state.animDuration * 3000
      )
    })
  }

  render() {
    return (
      <div >
         <Grid container spacing={7}  style={{paddingLeft: 0}}>
         <Grid item lg={9} sm={12} xs={12} style={{padding:11, paddingTop:8, paddingBottom: 16}} className="slider">
         <SliderC />
         </Grid>
         <Grid item lg={3} sm={12} xs={12} style={{paddingTop: 10}} className="mslider">
         <Grid item xs={6} sm={6} lg={12} ><div>
        <img id="rmdimg" src={ this.ramadanimg[this.state.backgroundIndex] } width="335px"/>
        </div></Grid>
         <Grid item xs={6} sm={6} lg={12} style={{ textAlign:"center"}} id="rmdtxt">
         {/* <span style={{fontSize: 30, fontFamily: "Aharoni", fontStyle: "italic"}}>Ramadan Offers</span>
         <br /> */}
         <span style={{fontSize: 33, fontFamily: 'Arial, Helvetica, sans-serif', fontStyle: "italic"}}></span></Grid>
         
         
         
         
         </Grid>
         <Grid item lg={12} xs={12}>
         <div style={{textAlign: 'center', fontSize: 36,fontStyle: "bold", fontFamily: 'Arial, Helvetica, sans-serif', color: '#00b39b'}} className="bheadings">ADVERTISING BUSINESS CLAN</div>
         
         </Grid>
         <Grid item lg={1} xs={12}></Grid>
         <Grid item lg={10} xs={12}><p style={{textAlign: 'center', fontSize: 25,fontFamily: 'Arial, Helvetica, sans-serif'}} className="mtext">Advertising Business Clan creates market awareness of the brands and provide information about their latest release, discounts and promotions.</p></Grid>
         <Grid item lg={1} xs={12}></Grid>
         {/* <Grid item lg={12} xs={12}>
         <img src="./assets/Best.png" style={{padding: 20, paddingLeft: 100}}></img>
         
         </Grid> */}
         
         
       {/* <Grid item xs={7}><HomeGrid /></Grid> */}
       <Grid item lg={6} xs={12}><Ads1 /></Grid>
         <Grid item lg={6} xs={12}><Ads2 /></Grid>
         <br />
        <Grid item lg={12} xs={12}>
        <BrandGrid />
        </Grid>
        <br />
        <br />
        <Grid item lg={6} xs={12}><Ads1 /></Grid>
         <Grid item lg={6} xs={12}><Ads2 /></Grid>
         <br />
        {/* <Grid item xs={12}>
        <BrandLogoGrid />
        </Grid> */}

<Grid item xs={12}>
        <Mall />
        </Grid>
        <br />
        {/* <Grid item xs={12}>
        <RedMallGrid />
        </Grid>
        <br />
        <Grid item lg={6} xs={12}><Ads1 /></Grid>
         <Grid item lg={6} xs={12}><Ads2 /></Grid>
         <br />
        
        <Grid item xs={12}>
        <GroceryGrid />
        </Grid> */}
        <br />
        
        <Grid item lg={6} xs={12}><Ads1 /></Grid>
         <Grid item lg={6} xs={12}><Ads2 /></Grid>
         <br />
        </Grid>
        <br />
      </div>
    )
  }
}
