import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import titleRedMallData from './titleredMallData';
import tileBrandData from '../BrandpageGrid/titleBrandData'
import Grid from '@material-ui/core/Grid';
import { Link } from 'react-router-dom'
import './BrandGrid.css'
import withWidth, { isWidthUp } from '@material-ui/core/withWidth';
import Ads1 from '../ads/ads1'
import Ads2 from '../ads/ads2'

import $ from 'jquery';

const styles = theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      // backgroundColor: theme.palette.background.paper,
    },
    gridList: {
      width: 1089,
      height: 330,
     

    },
  });
  class RedMallGrid extends Component {
    constructor(props){
        super(props)
        this.state = {
          data: titleRedMallData
        }
    }
    render(){
      const { classes } = this.props;
      const getGridListCols = () => {
        console.log("function")
        if (isWidthUp('xl', this.props.width)) {
          console.log('xl ')
          return 4;
        }
    
        if (isWidthUp('lg', this.props.width)) {
          console.log('lg ')
          return 3;
        }
    
        if (isWidthUp('md', this.props.width)) {
          console.log('md ')
          return 2;
        }
        if (isWidthUp('sm', this.props.width)) {
          return 2;
        }
        if (isWidthUp('xs', this.props.width)) {
          return 1;
        }
    
        return 1;
      }
      // let brand = titleRedMallData
     // let brand = titleRedMallData.filter(function(brand){  return brand.title=='okaidi'});
      
      console.log(this.state.data)
      
      const handleFilter = (event, value) => {
        console.log(event.target)
        $('.button-group').each( function( i, buttonGroup ) {
         
          var $buttonGroup = $( buttonGroup );
          
        
            
            $buttonGroup.find('.is-checked').removeClass('is-checked');
           
            $( event.target ).addClass('is-checked');
        
        });
        console.log(event.target.getAttribute('value'))
        if(event.target.getAttribute('value') === 'all'){
          this.setState({data: titleRedMallData})
        }
        else{
      let brand = titleRedMallData.filter(function(brand){  return brand.category==event.target.getAttribute('value')})
      this.setState({data: brand})
        
       console.log(brand)
      }
      // change is-checked class on buttons
  
      }
      
      return (
        <div className={classes.root}>
      <Grid item lg={6} xs={12}><Ads1 /></Grid>
           <Grid item lg={6} xs={12}><Ads2 /></Grid>
        <h2 style={{textAlign: "center"}}>Red Sea Mall Trends with just one click!</h2>
        <div className="ui-group">
  <div className="button-group js-radio-button-group" data-filter-group="subject">
  <button className="button is-checked" data-filter="" value="all" onClick={handleFilter} >All</button>
  <button className="button" data-filter=".typography" value="dept" onClick={handleFilter}>Department Stores</button>
  <button className="button" data-filter=".stocks" value="cosper" onClick={handleFilter}>Cosmetics / Perfumes</button>
  <button className="button" data-filter="" value="kidsfashion" onClick={handleFilter} >Kids Fashion</button>
  <button className="button" data-filter=".typography" value="mensfashion" onClick={handleFilter}>Mens Fashion</button>
  <button className="button" data-filter="" value="ladiesfashion" onClick={handleFilter} >Ladies Fashion</button>
  <button className="button" data-filter=".typography" value="shoes" onClick={handleFilter}>Shoes</button>
  <button className="button" data-filter="" value="watches" onClick={handleFilter} >Watches</button>
  <button className="button" data-filter=".typography" value="h/a" onClick={handleFilter}>Handbags / Accessories</button>
  <button className="button" data-filter=".typography" value="others" onClick={handleFilter}>Others</button>
  
  
  </div>
  </div>
          <GridList cellHeight={160} cols={getGridListCols} className="brandgrid">
            {this.state.data.map(tile => (
              <GridListTile key={tile.img} cols={tile.cols || 1}>
               <Link to={`/brandpage/${tile.title}`}> <img src={tile.img} alt={tile.title}  className={tile.css} /></Link>
              </GridListTile>
            ))}
          </GridList>
        </div>
      );
            }
    }

  RedMallGrid.propTypes = {
    classes: PropTypes.object.isRequired,
  };

  export default withStyles(styles)(RedMallGrid);
