import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import tileMallData from './titleMallData';
import { Link } from 'react-router-dom'
import './MallGrid.css'
import withWidth, { isWidthUp } from '@material-ui/core/withWidth';

const styles = theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      // backgroundColor: theme.palette.background.paper,
    },
    gridList: {
      width: 1089,
      height: 330,
     

    },
  });

  function Mall(props) {
    const { classes } = props;
    const getGridListCols = () => {
      console.log("function")
      if (isWidthUp('xl', props.width)) {
        console.log('xl ')
        return 2;
      }
  
      if (isWidthUp('lg', props.width)) {
        console.log('lg ')
        return 2;
      }
  
      if (isWidthUp('md', props.width)) {
        console.log('md ')
        return 2;
      }
      if (isWidthUp('sm', props.width)) {
        return 2;
      }
      if (isWidthUp('xs', props.width)) {
        return 1;
      }
  
      return 1;
    }
    
  
    return (
      <div className={classes.root}>
      <h2 style={{textAlign: "center",fontStyle: "bold", fontFamily: 'Arial, Helvetica, sans-serif', fontSize: 24, paddingBottom: 20}} className="bheadings">MALL TRENDS WITH JUST ONE CLICK! MORE COMING SOON.</h2>
        <GridList cellHeight={245} cols={getGridListCols} className="mallgrid">
          {tileMallData.map(tile => (
            <div className="mallbox">
            <GridListTile key={tile.img} cols={tile.cols || 1} >
             <Link to={`/${tile.title}`}> <img src={tile.img} alt={tile.title}  className="imagedropshadowmallgrid" /></Link>
             
            </GridListTile>
            <h2 style={{textAlign: "center", marginTop: 0}}>{tile.mall.toUpperCase()}</h2>
            </div>
          ))}
          
        </GridList>
      </div>
    );
  }

  Mall.propTypes = {
    classes: PropTypes.object.isRequired,
  };

  export default withStyles(styles)(Mall);
